.container {
     display: flex;
     justify-content: center;
     align-items: center;
     height: 100vh;
     background-color: #c8b43e; 
 }
 
 .inputForm {
     background-color: white;
     padding: 20px;
     border-radius: 10px;
     box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1); 
 }
 
 input[type="text"],
 input[type="email"],
 input[type="number"],
 input[type="url"],
 textarea {
     width: 100%;
     padding: 10px;
     margin-bottom: 10px;
     border: 1px solid #4de113;
     border-radius: 5px;
     box-sizing: border-box;
 }
 
 button {
     width: 100%;
     padding: 10px;
     background-color: #00ff04; 
     color: white;
     border: none;
     border-radius: 5px;
     cursor: pointer;
 }
 
 button:hover {
     background-color: #0cdac5; 
 }
 